.box {
  display: inline-block;
  width: 100px;
  height: 100px;
  border: 1px solid black;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }
  .box.blue {
    background: blue; }
  .box:hover {
    background: yellow; }
  .box:active {
    background: red; }
  .box .box-inside {
    background: black;
    width: 50px;
    height: 50px; }



